import axios from 'axios';

const App = ({data}) => (
    <div>
        <h1>ROBOFRIENDS</h1>
        <input type='text'></input>
    </div>
);

App.getInitialProps = async function() {
    const response = await axios.get('https://jsonplaceholder.typicode.com/users');
    return response.data;
}

export default App;